import React, { memo } from 'react';
import { useSelector, shallowEqual } from 'react-redux';
import HYPlayHeader from './c-cpns/play-header';
import HYPlayList from './c-cpns/play-list';
import HYLyricPanel from './c-cpns/lyric-panel';
import { PanelWrapper } from './style';



export default memo(function PlayPanel() {
  const { currentSong} = useSelector(state => ({
    currentSong: state.getIn(["player", "currentSong"]),
  }), shallowEqual);
  return (
    <PanelWrapper>
      <HYPlayHeader/>
      <div className="main">
        <img className="image" src={currentSong.al.picUrl} alt=""/>
        <HYPlayList/>
        <HYLyricPanel/>
      </div>
    </PanelWrapper>
  )
})
