export function scrollTo(element, to) {
  //看起来第三行代码可以使用数学公式 变为element.scrollTop = to
  //但实际上，to的值随着Index改变尔增大，INDEX值为8时 ， to为 150，此时当前scrollTop+to
  //会导致滑动距离过大
  let difference = to - element.scrollTop;
  //console.log(to ,element.scrollTop)
  element.scrollTop = element.scrollTop + difference;
  //console.log(element.scrollTop)
}

